import Layout from "../../components/layout";
import fetch from "isomorphic-unfetch";
import React, { Component } from "react";
import { getUserbyid, getUserwithCursosByUserId } from "../../API/helpersAPI";

class UserCursos extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <Layout>
        <h1>hola soy el CURSO del usuario : {this.props.queryby}</h1>
      </Layout>
    );
  }
}

UserCursos.getInitialProps = async ({ query }) => {
  console.log(query);
 
 var id_usuario = query.id_user.split('_')[1];  
 getUserwithCursosByUserId(id_usuario).then(response => { 
     console.log(response);
     console.log(JSON.stringify(response[0].cursos))
     }
);

 
   
  return { queryby: query.id_user  };
};
export default UserCursos;
